import React, { useState, useEffect } from 'react'
import { ButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';

import firebase from '../FirebaseConfig';
// Styles
import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux'
import { signIn, signOut } from '../Actions/UserAction'
import {
    Bar, NavDiv,
    NavLink, LoginStatus, SignoutBtn
} from '../StyledComponents/StyledComponents'

import { redirectPage } from '../Common/HelperFunctions'
import styled from 'styled-components'

const WatchlistLink = styled(Link)`
    color: black;
    &:hover{
        color: black;
        text-decoration: none;
    }
    &:focus{
        color: black;
        text-decoration: none;
    }
`

const ProfileDropdown = styled(ButtonDropdown)`
    border-style: none;
  
`
const ProfileMenu = styled(DropdownMenu)`
 
    padding: 0px;
    margin: 0px;
`

const ProfileLogout = styled(DropdownItem)`
    background-color: red;
    color: white;
     border: 2px solid red;
     transition: all 0.4s;
  
    &:hover{
        background-color: white;
        border: 2px solid red;
        color: red;
    }
`

const ProfileToggle = styled(DropdownToggle)`
    background-color: transparent;
    border-style: none;
    padding: 3px 8px;
    border-radius: 0px;
    &:focus{
        outline:none;
        background-color: transparent;
    }
    &:hover{
        outline:none;
        background-color: #3b883e;
    }

`

// Styles end
function Navbar() {
    const dispatch = useDispatch()
    const [data, setData] = useState()


    useEffect(() => {
        firebase.auth().onAuthStateChanged(user => {
            if (user) {
                setData(user)

            }
        })
    }, [])
    const [dropdownOpen, setOpen] = useState(false);



    const toggle = () => {
        setOpen(!dropdownOpen)
        dispatch({ type: 'DROPDOWN_TOGGLE', payload: !dropdownOpen })

    };

    const logoutHandler = () => {
        firebase.auth().signOut()
        dispatch(signOut())
        toggle()
        setData()
    }

    const darkModeHandler = () => {

    }

    const renderSigned = () => {
        if (data) {
            const link = `/watchhistory/${data.uid}`
            // return <NavLink to="/profile">{data.displayName}</NavLink>
            return <ProfileDropdown isOpen={dropdownOpen} toggle={toggle}>
                <ProfileToggle>
                    {data.displayName}
                </ProfileToggle>
                <ProfileMenu>
                    <DropdownItem header>Profile</DropdownItem>

                    <DropdownItem> <WatchlistLink to={link}>Watch history</WatchlistLink> </DropdownItem>
                    <DropdownItem> Darkmode </DropdownItem>
                    <DropdownItem divider />
                    <ProfileLogout onClick={logoutHandler} >Logout</ProfileLogout>
                </ProfileMenu>
            </ProfileDropdown>
        }
        return <>
            <NavLink to="/signin">Sign in</NavLink>
            {/* {redirectPage(true)} */}
        </>
    }

    return (
        <Bar>

            <NavDiv>
                <NavLink to="/">News</NavLink>
                <LoginStatus>
                    {renderSigned()}
                </LoginStatus>
            </NavDiv>
        </Bar>
    )
}

export default Navbar
