import React from 'react'
import Tag from './Tag'
import styled from 'styled-components'

const TagContainer = styled.div`
    display: flex;
    padding: 20px;
    align-items: center;
    border-bottom: 2px solid #00000029;
`

function TagsGroup() {

    // Recent
    // trending
    // all
    return (
        <TagContainer>
            <Tag color="#9c27b0" name="Recent" />
            <Tag color="#673ab7" name="Trending" />
            <Tag color="#3f51b5" name="All" />
        </TagContainer>
    )
}

export default TagsGroup
