import React from 'react'
import { Link } from 'react-router-dom'

import { Section, SubSection, H1, P, Button, SVGFrame } from '../StyledComponents/StyledComponents'
import Image from '../assets/newspaper.svg'
import styled from 'styled-components';
import Navbar from '../components/Navbar';



const Description = styled(P)`
    width: 250px;

`

function LandingPage() {
    return (
        <>
            <Navbar />

            <Section>
                <SVGFrame src={Image} />
                <SubSection>
                    <H1>News</H1>
                    <Description>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500</Description>

                    <Link to="/timeline">
                        <Button>Watch News</Button>
                    </Link>
                </SubSection>


            </Section>

        </>
    )
}

export default LandingPage
