import React, { useState, useEffect } from 'react'

import firebase from '../FirebaseConfig';
import axios from 'axios'

export const useGetData = (URI, isDeleted) => {
    const [data, setdata] = useState()
    const [err, seterr] = useState()
    useEffect(() => {

        firebase.auth().onAuthStateChanged(user => {
            if (user) {
                let URIarr = URI.split('/')
                if (URIarr.includes('watchhistory')) {
                    let URIarr = URI.split('/')
                    let URI_UID = URIarr[URIarr.length - 1]

                    if (URI_UID !== user.uid) {
                        URIarr.pop()
                        URIarr.push(user.uid)
                        const userURI = URIarr.join('/')
                        console.log(userURI)
                        user.getIdToken(true)
                            .then(token => {
                                axios.get(userURI, {
                                    headers: {
                                        'Authorization': token
                                    }
                                })
                                    .then(({ data }) => setdata(data))
                                    .catch(err => {
                                        seterr(err)
                                    })
                            })
                            .catch(err => seterr(err))

                    } else {

                        user.getIdToken(true)
                            .then(token => {
                                axios.get(URI, {
                                    headers: {
                                        'Authorization': token
                                    }
                                })
                                    .then(({ data }) => setdata(data))
                                    .catch(err => {
                                        seterr(err)
                                    })
                            })
                            .catch(err => seterr(err))



                    }

                } else {

                    console.log(URI)
                    user.getIdToken(true)
                        .then(token => {
                            axios.get(URI, {
                                headers: {
                                    'Authorization': token
                                }
                            })
                                .then(({ data }) => setdata(data))
                                .catch(err => {
                                    seterr(err)
                                })
                        })
                        .catch(err => seterr(err))





                }



            } else {
                seterr('Nouser')
            }
        })
    }, [URI, isDeleted]);

    return [data, err];
}

export const PostUserData = (URI, data, token) => {
    // useEffect(() => {
    axios.post(URI, data, {
        headers: {
            'Authorization': token
        }
    }).then(data => {
        console.log('Data sent')
    }).catch(err => {
        firebase.auth().signOut()
        console.log(err)
    })

}

export const useAddWatchlist = (URI, videoID) => {
    useEffect(() => {
        firebase.auth().onAuthStateChanged(user => {
            if (user) {
                const newURI = `${URI}/${user.uid}`
                user.getIdToken(true)
                    .then(token => {
                        axios.patch(newURI, { videoID }, {
                            headers: {
                                'Authorization': token
                            }
                        })
                            .then(({ data }) => console.log('Added to watchlist'))
                            .catch(err => {
                                console.log(err)
                            })
                    })
                    .catch(err => console.log(err))
            }
        })
    }, [videoID]);
}


export const useGetRecommendation = (URI) => {
    const [data, setData] = useState()
    const [err, seterr] = useState()

    useEffect(() => {
        firebase.auth().onAuthStateChanged(user => {
            if (user) {
                const newURI = `${URI}/${user.uid}`
                user.getIdToken(true)
                    .then(token => {
                        axios.get(newURI, {
                            headers: {
                                'Authorization': token
                            }
                        })
                            .then(({ data }) => setData(data))
                            .catch(err => {
                                seterr(err)
                            })
                    })
                    .catch(err => seterr(err))
            } else {
                seterr(err)
            }
        })
    }, [URI]);

    return [data, err]
}