import React from 'react'
import styled from 'styled-components'
import NewsItem from './NewsItem'
import { redirectPage } from '../Common/HelperFunctions'
import { UnorderedList } from '../StyledComponents/StyledComponents'
import { Spinner } from 'reactstrap';
import EmptySVG from '../assets/empty.svg'

const DisplayContainer = styled.div`
    max-width: 600px;
    margin: 0 auto;

`

const EmptyVideoContainer = styled.div`


`

const EmptyImage = styled.img`
    width: 70%;
`

const EmptyMessage = styled.p`
    margin: 0px;
    padding: 0px;
    font-weight: 800;
    margin: 20px;
    font-size: 1.3em;
`

function VideoList({ data, err }) {

    const renderNews = () => {
        if (data) {
            if (data.length > 0) {
                return data.map(item => {
                    return <NewsItem key={item.key} item={item} />
                })
            }
            return <div>
                <EmptyImage src={EmptySVG} />
                <EmptyMessage>No video</EmptyMessage>

            </div>
        } else {
            return <Spinner type="grow" color="success" />
        }
    }

    return (
        <div>
            <DisplayContainer>


                <UnorderedList>
                    {
                        renderNews()
                    }
                </UnorderedList>


            </DisplayContainer>

            {redirectPage(err)}
        </div>
    )
}

export default VideoList
