import React, { useEffect } from 'react'
import Navbar from '../components/Navbar'
import { useGetData, useAddWatchlist, useGetRecommendation } from '../Hooks/Hooks'
import styled from 'styled-components'
import { UnorderedList } from '../StyledComponents/StyledComponents'
import { formatDate } from '../Common/HelperFunctions'
import NewsItem from '../components/NewsItem'
import { redirectPage } from '../Common/HelperFunctions'
import axios from 'axios'
import { Spinner } from 'reactstrap';
import { useSelector } from 'react-redux'
import { proLink } from '../Endpoints'

const NewsContainer = styled.div`
    max-width: 600px;
    margin: 0px auto;

`
const StyledPlayer = styled.video`
    /* box-shadow: 5px 2px 19px 1px #0000007d; */
    position: relative;
    z-index: ${props => props && props.isDropdownOpen ? -1 : 1};
    width: 100%;
    transition: all 0.4s;
    &:focus{
        outline: none;
    }

    &:hover{
        box-shadow: 5px 2px 19px 1px #0000007d;
    }
`

const Headline = styled.h2`
    font-weight: bold;
    text-align: left;
    padding: 0px;
     @media (max-width: 600px){
        padding: 0px 10px;
    }
`

const Date = styled.p`
    margin: 0px;
    padding: 0px;
    text-align: left;
    @media (max-width: 600px){
        padding: 0px 10px;
    }
`

const Fullstory = styled.p`
     margin: 0px;
    padding: 0px;
     margin-top: 10px;
    text-align: left;
    @media (max-width: 600px){
        padding: 0px 10px;
    }
`

const InfoContainer = styled.div`
    padding: 20px 0px;
    border-bottom: 1px solid #00000054;

`
const More = styled.h4`
margin-top: 20px;
    font-weight: bold;
    text-align: left;
    padding: 0px;
     @media (max-width: 600px){
        padding: 0px 10px;
    }
`

function FullNews(props) {
    const dropdownState = useSelector(state => state.userData)

    const id = props.match.params.id
    console.log(id)
    const URI = `${proLink}/news`
    const userURI = `${proLink}/user`
    const recommendaionURI = `${proLink}/recommendation`
    useAddWatchlist(userURI, id)


    const [data, err] = useGetData(`${URI}/${id}`)

    const renderPlayer = () => data ? <StyledPlayer isDropdownOpen={dropdownState.isOpen} controls={true} src={data.videoLink} /> : null
    const renderImage = () => data ? <img src={data.thumbnailLink}></img> : null
    const renderHeadline = () => data ? <Headline>{data.headline}</Headline> : null
    const renderDate = () => data ? <Date>{formatDate(data.date)}</Date> : null
    const renderFullstory = () => data ? <Fullstory>{data.fullStory}</Fullstory> : null
    return (
        <div>
            {redirectPage(err)}
            <Navbar />

            {data ?
                <NewsContainer>
                    {renderPlayer()}
                    <InfoContainer>
                        {renderHeadline()}
                        {renderDate()}
                        {renderFullstory()}
                    </InfoContainer>

                    <div>
                        <More>More Like this.</More>
                        <p style={{ textAlign: 'left' }}>Under development</p>
                        {/* <UnorderedList>
                            {
                                // renderNews()
                            }

                        </UnorderedList> */}
                    </div>


                </NewsContainer> : <div style={{ marginTop: '20px' }}> <Spinner type="grow" color="success" />
                </div>
            }
        </div>
    )
}

export default FullNews
