import React from 'react'
import { Redirect } from 'react-router-dom'

export const formatDate = (dateStr) => {
    const dateArr = new window.Date(dateStr).toDateString().split(' ')
    dateArr.shift()
    return `${dateArr[1]} ${dateArr[0]} ${dateArr[2]}`

}

export const textProcessor = (str, limit) => {
    if (str.length > limit) {
        const trimmed = str.slice(0, limit)
        return `${trimmed}...`
    } else {
        return str
    }
}


export const redirectPage = (err) => {
    return err ? <Redirect to="/signin" /> : null
}

export const redirectIfAuthenticated = (val) => {
    return val ? <Redirect to="/timeline" /> : null
}