import React from 'react'
import styled from 'styled-components'

import { Link, Redirect } from 'react-router-dom'
import { formatDate, textProcessor } from '../Common/HelperFunctions'

const Items = styled.li`
    list-style: none;
    /* border: 1px solid black; */
    border-bottom: 1px solid #00000047;
    padding: 20px 0px;
   
`
const Thumbnail = styled.img`
    width: 100px;
    height: 100px;
    border-radius: 6px;
`

const Headline = styled.h4`
    font-weight: 700;
    padding: 0px 20px 0px 0px;
`
const ContentDiv = styled.div`
    text-align: left;
`
const Date = styled.p`
padding: 0px;
margin: 0px;
    font-weight: 700;
    font-size: 0.8em;
    color: grey;
`

const Fullstory = styled.p`
    font-size: 0.9em;
    padding: 0px 20px 0px 0px;
`

const StyledLink = styled(Link)`
    text-decoration: none;
     display: flex;
    justify-content: space-between;
    color: black;
    &:hover{
        text-decoration: none;
        color: black;
    }
    

`

function NewsItem({ item }) {
    let { headline, thumbnailLink, _id, fullStory, date } = item
    const dateStr = formatDate(date)

    headline = textProcessor(headline, 30)
    fullStory = textProcessor(fullStory, 60)
    return (

        <Items>
            <StyledLink to={`/timeline/${_id}`}>
                <ContentDiv>
                    <Headline>{headline}</Headline>
                    <Fullstory>{fullStory}</Fullstory>
                    <Date>{dateStr}</Date>
                </ContentDiv>
                <div>
                    <Thumbnail src={thumbnailLink} />
                </div>
            </StyledLink>
        </Items>
    )
}

export default NewsItem
