import styled from 'styled-components';
import { Link } from 'react-router-dom'

const themeColor = `#4caf50`

const theme = {
    light: {
        background: '#e6e6e6',
        element: 'white',
        text: 'black'
    },

    dark: {
        background: '#292929',
        element: '#333333',
        text: '#ffffff82'
    }
}

const size = {
    mobileS: '320px',
    mobileM: '375px',
    mobileL: '425px',
    tablet: '768px',
    laptop: '1024px',
    laptopL: '1440px',
    desktop: '2560px'
}

export const device = {
    mobileS: `(max-width: ${size.mobileS})`,
    mobileM: `(max-width: ${size.mobileM})`,
    mobileL: `(max-width: ${size.mobileL})`,
    tablet: `(max-width: ${size.tablet})`,
    laptop: `(max-width: ${size.laptop})`,
    laptopL: `(max-width: ${size.laptopL})`,
    desktop: `(max-width: ${size.desktop})`,
    desktopL: `(max-width: ${size.desktop})`
};


export const Ul = styled.ul`
    display: flex;
    background-color: ${themeColor};
    justify-content: space-between;
    padding: 0px 70px;
    box-shadow: 0px 3px 11px 1px #00000063;
`
export const FlexDiv = styled.div`
    display: flex;
`

export const Li = styled.li`
    list-style: none;
    padding: 5px 20px;
    font-weight: bold;
    color: white;
`

export const Section = styled.div`
    margin: auto;
    margin-top: 10%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex: 1;
    max-width: 1000px;
    @media ${device.mobileL}{
        flex-direction: column
    }
`

export const SubSection = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
`

export const H1 = styled.h1`
    font-size: 3.2em;
    font-weight: bold;
    color: grey;
`

export const P = styled.p`
    text-align: left;
    
`

export const Button = styled.button`
    font-weight: bold;
    background-color: ${themeColor};
    border: 2px solid ${themeColor};
    padding: 7px 20px;
    font-weight: bold;
    color: white;
    border-radius: 8px;
    
    transition: all 0.4s;
    &:hover{
        background-color: white;
        border: 2px solid ${themeColor};
        color: ${themeColor};
    }

    &:focus{
        outline: none;
    }



`

export const SVGFrame = styled.img`
    width: 360px;
    @media ${device.mobileM}{
        width: 300px;
        margin-bottom: 20px;
    }
`

export const Card = styled.div`
    background-color: white;
    width: fit-content;
    box-shadow: 4px 4px 19px 1px #00000059;
    padding: 20px;
`

export const StyledLink = styled(Link)`
    color: white;
    text-decoration: none;
     &:hover {
    
  }
  &.active {
    color: red;
  }

`


export const Bar = styled.div`
    
    
    
    background-color: ${themeColor};
    position: sticky;
    box-shadow: 0px 2px 6px 1px #00000063;
    height: 30px;
`
export const NavDiv = styled.div`

    max-width: 600px;
    margin: auto;
    padding: 0px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
`
export const NavLink = styled(Link)`
    color: white;
    font-weight: bold;
    &:hover{
        color: white;
        text-decoration:none;
    }
     &:focus{
        color: white;
        text-decoration:none;
    }
`
export const LoginStatus = styled.div`
    display: flex;
`
export const SignoutBtn = styled.img`
margin: 3px 0px 0px 14px;
    height: 20px;
    width: 20px;
`

export const StyledTag = styled.button`
    background-color: ${props => props.color};
    border-style: none;
    padding: 2px 0px;
    width: 70px;
    border-radius: 50px;
    color: white;
    font-size: 0.8em;
    font-weight: bold;
    margin-right: 10px;
    &:focus{
        outline: none;
    }
`

export const UnorderedList = styled.ul`
    padding: 20px;
    margin: 0px;
`
