const initialState = {
    isAuthenticated: false,
    data: {}

}
const AuthReducer = (state = initialState, { type, payload }) => {
    switch (type) {
        case 'SIGN_IN':
            state = {
                data: payload,
                isAuthenticated: true
            }
            return state

        case 'SIGN_OUT':
            state = {
                ...state,
                isAuthenticated: false
            }
            return state

        default:
            return state;
    }

}

export default AuthReducer;