import React, { useState, useEffect } from 'react'
import Navbar from '../components/Navbar'
import { proLink } from '../Endpoints'
import { useGetData } from '../Hooks/Hooks'
import VideoList from '../components/VideoList'
import axios from 'axios'
import styled from 'styled-components'
import ClearSVG from '../assets/clear_all.svg'
import { Spinner } from 'reactstrap'

const ClearContainer = styled.div`
    max-width: 600px;
    margin: auto;
    padding:20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 2px solid #00000029;
`

const WatchHistoryLabel = styled.p`
    margin: 0px;
    padding: 0px;
    font-weight: 800;
    font-size: 1.2em;
    color: #00000096;

`

const ClearBtn = styled.button`
        border-style: none;
    background-color: #d8d8d8;
    border: 2px solid #d8d8d8;
    padding: 2px 12px;
    font-weight: 600;
    font-size: 0.9em;
    border-radius: 33px;
    &:hover{
        background-color: white;
    }

    &:focus{
        outline: none;
    }

`

function WatchHistory(props) {
    const id = props.match.params.id
    const [isDeleted, setisDeleted] = useState(false)
    const URI = `${proLink}/watchhistory/${id}`

    const deleteHandler = async () => {
        try {
            await axios.delete(URI)
            console.log('Deleted')
            setisDeleted(true)
        } catch (err) {
            console.log(err)
        }
    }
    console.log(isDeleted)
    let [data, err] = useGetData(URI, isDeleted)

    const clearBtn = () => {
        if (data) {
            if (data.length > 0) {
                if (isDeleted) {
                    return <Spinner type="grow" color="danger" />
                }
                return <ClearBtn onClick={deleteHandler}>Clear History <img src={ClearSVG} /></ClearBtn>
            } else {

                return null
            }
        }
    }
    return (
        <div>
            <Navbar />
            <ClearContainer>
                <WatchHistoryLabel>Watch History</WatchHistoryLabel>
                {clearBtn()}
            </ClearContainer>
            <VideoList data={data} err={err} />
        </div>
    )
}

export default WatchHistory
