import React, { useEffect, useState } from 'react'

import axios from 'axios'
import firebase from '../FirebaseConfig';
import StyledFirebaseAuth from 'react-firebaseui/StyledFirebaseAuth'

import { redirectIfAuthenticated } from '../Common/HelperFunctions'
import { proLink } from '../Endpoints'

// https://news-app-api-vivek11101997.herokuapp.com/

function Auth() {

    const [isAuthenticated, setisAuthenticated] = useState(false)
    const [tokenID, settokenID] = useState('')
    const [userData, setUserData] = useState({})

    useEffect(() => {
        console.log('useEffect 1....')
        // console.log(firebase.auth().currentUser)
        firebase.auth().onAuthStateChanged(user => {
            if (user) {
                setUserData(user)
                user.getIdToken(true)
                    .then(token => {
                        settokenID(token)
                    })
                    .catch(err => console.log(err))
            }
        })
    }, [])


    useEffect(() => {
        console.log('useEffect 2....')
        const createUserEndpoint = `${proLink}/user`

        if (tokenID && userData) {
            const data = {
                firebase_uid: userData.uid,
                name: userData.displayName,
                email: userData.email,
                isDarkMode: false
            }
            axios.post(createUserEndpoint, data, {
                headers: {
                    'Authorization': tokenID
                }
            })
                .then(data => {
                    setisAuthenticated(true)
                    console.log('Data sent')
                })
                .catch(err => {
                    console.log('There is some error while login')
                    setisAuthenticated(false)
                    firebase.auth().signOut()
                    console.log(err)
                })

        }

    }, [tokenID])





    const uiConfig = {

        signInFlow: "popup",

        signInOptions: [
            firebase.auth.GoogleAuthProvider.PROVIDER_ID,
            firebase.auth.FacebookAuthProvider.PROVIDER_ID,
            firebase.auth.EmailAuthProvider.PROVIDER_ID
        ],

        callbacks: {
            signInSuccess: () => false
        }

    }





    return (
        <>
            {redirectIfAuthenticated(isAuthenticated)}
            <StyledFirebaseAuth
                uiConfig={uiConfig}
                firebaseAuth={firebase.auth()}
            />
        </>
    )
}

export default Auth
